import Vue from 'vue'
import Router from 'vue-router'

import Home from '../view/home.vue' // 首页 '
import Products from '../view/Products.vue' // 产品
import video from '../view/video.vue' // 视频
import News from '../view/news.vue' // 视频
import FAQS from '../view/FAQS.vue' // 常见问题
import contact from '../view/contact.vue' // 联系我们
//
import torsionspring from '../view/torsion-spring.vue' // torsion-spring
import DVTCompressionSpring from '../view/DVT-Compression-Spring.vue'
import NingboFenghuaDVTSpringColtd from '../view/Ningbo Fenghua DVT Spring Co., Ltd.vue'
import CustomizedCompressionSprings from '../view/Customized Compression Springs.vue'

Vue.use(Router)
import { Loading } from 'element-ui';
let loadingInstance = Loading.service(true)

let spinRoute = {
  show() {//加载中显示loading组件
    console.log(loadingInstance)//开启loading组件
  },
  resolve(resolve) {//加载完成隐藏loading组件
    return component => {
      setTimeout(() => {
        loadingInstance.close();
        resolve(component);
      }, 1500)
    }
  }
}
export default new Router({
  //mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/Home'
    },
    {
      path: '/Home',
      name: 'Home',
      component: resolve => {
        spinRoute.show();//加载时开启loading
        require(['../view/home.vue'], spinRoute.resolve(resolve))//路由懒加载
      },
      meta: {
        title: 'Home'
      },


    },
    {
      path: '/Products',
      name: 'Products',
      component: resolve => {
        spinRoute.show();//加载时开启loading
        require(['../view/Products.vue'], spinRoute.resolve(resolve))//路由懒加载
      },
      meta: {
        title: 'Products'
      },
    },
    {
      path: '/video',
      name: 'video',
      component: resolve => {
        spinRoute.show();//加载时开启loading
        require(['../view/video.vue'], spinRoute.resolve(resolve))//路由懒加载
      },
      meta: {
        title: 'video'
      },
    },
    {
      path: '/News',
      name: 'News',
      component: resolve => {
        spinRoute.show();//加载时开启loading
        require(['../view/news.vue'], spinRoute.resolve(resolve))//路由懒加载
      },
      meta: {
        title: 'News'
      },
    },
    {
      path: '/FAQS',
      name: 'FAQS',
      component: resolve => {
        spinRoute.show();//加载时开启loading
        require(['../view/FAQS.vue'], spinRoute.resolve(resolve))//路由懒加载
      },
      meta: {
        title: 'FAQS'
      },
    },
    {
      path: '/contact',
      name: 'contact',
      component: resolve => {
        spinRoute.show();//加载时开启loading
        require(['../view/contact.vue'], spinRoute.resolve(resolve))//路由懒加载
      },
      meta: {
        title: 'contact'
      },
    },
    {
      path: '/News/torsion-spring',
      name: 'torsionspring',
      component: resolve => {
        spinRoute.show();//加载时开启loading
        require(['../view/torsion-spring.vue'], spinRoute.resolve(resolve))//路由懒加载
      },
      meta: {
        title: 'torsionspring'
      },
    },
    {
      path: '/News/DVT-Compression-Spring',
      name: 'DVTCompressionSpring',
      component: resolve => {
        spinRoute.show();//加载时开启loading
        require(['../view//DVT-Compression-Spring.vue'], spinRoute.resolve(resolve))//路由懒加载
      },
      meta: {
        title: 'DVTCompressionSpring'
      },
    },
    {
      path: '/News/Ningbo-Fenghua-DVT-Spring-Co-ltd',
      name: 'NingboFenghuaDVTSpringColtd',
      component: resolve => {
        spinRoute.show();//加载时开启loading
        require(['../view/Ningbo Fenghua DVT Spring Co., Ltd.vue'], spinRoute.resolve(resolve))//路由懒加载
      },
      meta: {
        title: 'NingboFenghuaDVTSpringColtd'
      },
    },
    {
      path: '/Products/Customized-Compression-Springs',
      name: 'CustomizedCompressionSprings',
      component: resolve => {
        spinRoute.show();//加载时开启loading
        require(["../view/Customized Compression Springs.vue"], spinRoute.resolve(resolve))//路由懒加载
      },
      meta: {
        title: 'CustomizedCompressionSprings'
      },
    }
  ],
});


